import React, { useState, useEffect } from "react";
import { Image } from "react-native";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";
import { CiEdit } from "react-icons/ci";
import { TiTick } from "react-icons/ti";
import { InputGroup, Stack } from "react-bootstrap";
import { toast } from "react-toastify";
import axios from "axios";
function PersonalInfo() {
  const [image,setImage]=useState("data:image/webp;base64,");
  
  const [firstname,setFirstname]=useState("");
  const [lastname,setLastname]=useState("");
  const [email,setEmail]=useState("");
  const [phone,setPhone]=useState(null);
  const [dob,setDob]=useState("");
  const [country,setCountry]=useState("");
  const [loading,setLoading]=useState(false);
  const [editOption, setEditOption] = useState(false);
  const [newImage, setNewImage] = useState(null);

  useEffect(()=>{
    setLoading(false);
    axios.get("http://localhost:8080/jpa/get-users/"+localStorage.getItem("isLoggedIn"))
    .then(response=>{
        
          response.data.image?setImage(response.data.image):setImage(image);
          setFirstname(response.data.firstName)
          setLastname(response.data.lastName)
          setEmail(response.data.email)
          setPhone(response.data.phone)
          setDob(response.data.dob)
          setCountry(response.data.country)
        
    })
    setLoading(true);
  },[])


  const save_changes=()=>{
    const formdata={
      "image":newImage,
      "firstName":firstname,
      "lastName":lastname,
      "country":country,
      "email":email,
      "phone":phone,
      "dob":dob

    }
    console.log(formdata);
      axios.put("http://localhost:8080/jpa/update-info/"+localStorage.getItem("isLoggedIn"),formdata)
      .catch(error=>console.log(error));
  }


  //   useEffect(() => {
  //     // Fetch user details including images when component mounts
  //     const fetchUserData = async () => {
  //       try {
  //         // Fetch user details from API
  //         const response = await fetch("https://api.example.com/user/details");
  //         const userData = await response.json();
  //         setUserData(userData);

  //         // Retrieve username from localStorage
  //         const storedUsername = localStorage.getItem("username");
  //         setUsername(storedUsername);
  //       } catch (error) {
  //         console.error("Error fetching user details:", error);
  //       }
  //     };

  //     fetchUserData();
  //   }, []);

  const handleEditChange = () => {
    setEditOption(!editOption);
    if (!editOption) {
      toast("You can edit the fields now");
    }
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onloadend = () => {
      setNewImage(reader.result);
    };

    if (file) {
      reader.readAsDataURL(file);
    }
  };

  return (
    <div>
      {loading ? (
        <div>
          <Container>
            <Row>
              <Stack direction="horizontal" className="justify-content-between">
                <h1 style={{ marginTop: "15px", fontWeight: "600" }}>
                  Personal Info
                </h1>
                {!editOption ? (
                  <Button variant="dark" onClick={handleEditChange}>
                    <CiEdit style={{ marginRight: "5px" }} />
                    Edit
                  </Button>
                ) : (
                  <Button variant="success" onClick={()=>{
                    handleEditChange();
                    save_changes();
                  }}>
                    <TiTick style={{ marginRight: "5px" }} />
                    Save
                  </Button>
                )}
              </Stack>
            </Row>
            <Row className="justify-content-center give-both-margins">
              {editOption && (
                <input
                  id="file-upload"
                  type="file"
                  accept="image/*"
                  onChange={handleImageChange}
                  style={{ display: "none" }}
                />
              )}

              <Image
                style={{
                  width: 200,
                  height: 200,
                  borderRadius: 200 / 2,
                  cursor: "pointer",
                }}
                source={newImage ? newImage :image}
                onClick={() =>
                  editOption && document.getElementById("file-upload").click()
                }
              />
            </Row>

            <Row className="give-both-margins">
              <Row>
                <Col sm={6}>
                  <Form.Label>First Name</Form.Label>
                  <Form.Control
                    readOnly={!editOption}
                    defaultValue={firstname}
                    type="text"
                    onChange={(e)=>setFirstname(e.target.value)}
                  />
                </Col>
                <Col sm={6}>
                  <Form.Label>Last Name</Form.Label>
                  <Form.Control
                    readOnly={!editOption}
                    defaultValue={lastname}
                    type="text"
                    onChange={(e)=>setLastname(e.target.value)}
                  />
                </Col>
              </Row>
              <Row>
                <Col sm={6}>
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    readOnly
                    defaultValue={email}
                    type="text"
                    onChange={(e)=>setEmail(e.target.value)}
                  />
                </Col>
                <Col sm={6}>
                  <Form.Label>Phone Number</Form.Label>
                  <InputGroup>
                    <InputGroup.Text id="basic-addon1">+91</InputGroup.Text>
                    <Form.Control
                      readOnly={!editOption}
                      defaultValue={phone}
                      type="text"
                      onChange={(e)=>setPhone(e.target.value)}
                    />
                  </InputGroup>
                </Col>
              </Row>
              <Row>
                <Col sm={6}>
                  <Form.Label>Country</Form.Label>
                  <Form.Control
                    readOnly={!editOption}
                    defaultValue={country}
                    type="text"
                    onChange={(e)=>setCountry(e.target.value)}
                  />
                </Col>
                <Col sm={6}>
                  <Form.Label>Date of Birth</Form.Label>
                  <Form.Control
                    readOnly={!editOption}
                    defaultValue={dob}
                    type="date"
                    onChange={(e)=>setDob(e.target.value)}
                  />
                </Col>
              </Row>
            </Row>
            <Row>
              <Alert key="info" variant="info">
                <Alert.Heading>Delete your Account</Alert.Heading>
                <hr />
                <p>
                  Your would have another 14 day window to maintain your Account
                </p>
                <Button variant="danger">Delete</Button>
              </Alert>
            </Row>
          </Container>
        </div>
      ) : (
        <p>Loading...</p>
      )}
    </div>
  );
}

export default PersonalInfo;
